
/**
 * Paging Limt class
 */
export class FopPagingLimtModel {
	take:number;
	skip:number;
	limitOptions:any[];

	/**
	 *
	 * @param {number} skip skip items
	 * @param {number} take take items
	 * @param {string} allName name from resources
	 */
	constructor(skip :number=0, take:number=10) {
		this.skip = skip,
			this.take = take;
        this.limitOptions = [{ limit: 5, name: '5' },
        { limit: 10, name: '10' },
        { limit: 20, name: '20' },
        { limit: 70, name: '70' },
        { limit: 100, name: '100' },
        { limit: -1, name: 'All' }];
	}
}
   